<template>
    <div class="row m-3">
        <div class="col-12 " v-if="isAuth">
            <TitleTwo title="This is your Wallet !" />
            <canvas id="myChart"></canvas>
        </div>
        <div v-else>
            <TitleTwo title="You are not authenticated." />
            <ParagraphSimple class="w-text d-inline" text="please " />
            <ButtonSmall class="w-text " link="/login" nameBtn="Login" />
            <ParagraphSimple class="w-text d-inline" text=" or " />
            <ButtonSmall class="w-text " link="/register" nameBtn="Register" />
        </div>
    </div>

</template>

<script>
import Chart from 'chart.js/auto';
import axios from "axios";
import TitleTwo from '@/components/Title/TitleTwo.vue';
import ParagraphSimple from '@/components/Paragraph/ParagraphSimple.vue';
import ButtonSmall from '@/components/Button/ButtonSmall.vue';
export default {
    name: "UserWallet",
    data() {
        return {
            isAuth: this.$store.state.isAuth,
            wallet: null,
        };
    },
    created() {
        this.isAuth = this.$store.state.isAuth;
    },
    mounted() {
        this.isAuth = this.$store.state.isAuth;
        var listWallet = [];
        var freeWallet = [];
        var coloR = [];
        var dynamicColors = function () {
            var r = Math.floor(Math.random() * 255);
            var g = Math.floor(Math.random() * 255);
            var b = Math.floor(Math.random() * 255);
            return "rgb(" + r + "," + g + "," + b + ")";
        };
        axios.get("accounts/api/wallet/").then((response) => {
            this.wallet = response.data.keys.tansaction;
            this.wallet = JSON.parse(this.wallet);
            this.wallet.forEach(element => {
                listWallet.push(element.asset);
                freeWallet.push(element.free);
                coloR.push(dynamicColors());
            });
            console.log(freeWallet);
            console.log(listWallet);
            display();
        }).catch((error) => {
            console.log(error);
        });
        var display = function () {
            const labels = listWallet;
            const data = {
                labels: labels,
                datasets: [{
                    label: "Wallet",
                    data: freeWallet,
                    backgroundColor: coloR,
                    hoverOffset: 4
                }]
            };
            const config = {
                type: "pie",
                data: data,
            };
            const myChart = new Chart(document.getElementById("myChart"), config);
            myChart;
        };
    },
    components: { TitleTwo, ParagraphSimple, ButtonSmall }
}
</script>

<style>
.w-text {
    font-size: 1.2em;

}

.center {
    margin-left: 250px;
    position: absolute;
}

div.left {
    position: inherit;
}
</style>