<template>
    <div class="row m-3">
        <div  v-if="isAuth" class="col-12">
            <TitleTwo title="Please configure your Binance API" />
            <br>
            <form @submit.prevent="submitConfigForm">
                <Label labelName="APIkey" />
                <FormField v-model="ConfigData.config_Key" typeField="text" labelName="{{ ConfigData.config_Key }}"
                    nameField="api-key" />
                <Label labelName="Secret key" />
                <FormField v-model="ConfigData.config_PKey" typeField="text" labelName="{{ ConfigData.config_PKey }}"
                    nameField="config-PKey" />
                <ButtonFormSubmit nameBtn="Config" />
            </form>
            <br><br>
            <ButtonLink link="/" nameLink="Home" />
        </div>
        <div v-else>

            <TitleTwo title="You are not authenticated." />
            <ParagraphSimple class="w-text d-inline" text="please " />
            <ButtonSmall class="w-text " link="/login" nameBtn="Login" />
            <ParagraphSimple class="w-text d-inline" text=" or " />
            <ButtonSmall class="w-text " link="/register" nameBtn="Register" />
        </div>

        <!--{% if user.is_authenticated %}
        <h2>Please add here your binance keys</h2>
        <p>APIkey : {{keys.API_key}}</p>
        <p>tradingViewKey : {{keys.tradingViewKey}}</p>
        <p>id {{user.id}}</p>
        <p> porte feuille: beaucoup trop </p>
        {% if form.subject.errors %}
            <ol>
            {% for error in form.subject.errors %}
                <li><strong>{{ error|escape }}</strong></li>
            {% endfor %}
            </ol>
        {% endif %}    
        <form method="POST">
            {% csrf_token %}
            {{ form }}
            <input type="submit" value="Submit">
        </form>
        <a href="{% url 'home' %}"><button class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm">home</button></a>
    {% else %}-->
    </div>
</template>

<script>
import axios from "axios";
import TitleTwo from "@/components/Title/TitleTwo.vue";
import ParagraphSimple from "@/components/Paragraph/ParagraphSimple.vue";
import ButtonSmall from "@/components/Button/ButtonSmall.vue";
import ButtonFormSubmit from "@/components/Button/ButtonFormSubmit.vue";
import Label from "@/components/Form/Label.vue";
import ButtonLink from "@/components/Button/ButtonLink.vue";
import FormField from "@/components/FormField.vue";
export default {
    name: "UserConfigurations",
    data() {
        return {
            isAuth: this.$store.state.isAuth,
            ConfigData: {
                config_Key: null,
                config_PKey: null,
            },
        };
    },
    created() {
        this.isAuth = this.$store.state.isAuth;
    },
    mounted() {
        this.isAuth = this.$store.state.isAuth;
        axios.get("accounts/api/configuration/").then((response) => {
            console.log(response.data);
            this.ConfigData.config_Key = response.data;
            this.ConfigData.config_Key = this.ConfigData.config_Key["keys"].API_key;
            this.ConfigData.config_PKey = response.data;
            this.ConfigData.config_PKey = this.ConfigData.config_PKey["keys"].secret_key;
        });
    },
    methods: {
        submitConfigForm() {
            axios
                .post("accounts/api/configuration/set/", this.ConfigData)
                .then((response) => {
                    console.log(response);
                    this.$router.push("/dashboard");
                })
                .catch((error) => {
                    console.log(error);
                });
        },
    },
    components: { TitleTwo, ParagraphSimple, ButtonSmall, ButtonFormSubmit, Label, ButtonLink, FormField }
}
</script>

<style scoped>
.w-text {
    font-size: 1.2em;

}

.center {
    margin-left: 250px;
    position: absolute;
}

div.left {
    position: inherit;
}

div.form {
    text-align: left;
    position: absolute;
}
</style>