<template>
    <div class="row m-3">
        <div class="col-12">
            <TitleOne title="Welcome" />
            <div class="table">
                <table class="table">
                    <thead>
                        <tr>
                            Transactions :
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in info" :key="item.id">
                            {{ item }}
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

    </div>

</template>

<script>
import axios from "axios";
import TitleOne from "@/components/Title/TitleOne.vue";
export default {
    name: "DashBoard",
    data() {
        return {
            info: null,
        };
    },
    mounted() {
        axios.get("accounts/api/transactions/").then((response) => (this.info = response.data["keys"]));
        console.log(this.info);
    },
    components: { TitleOne }
}
</script>

