import { createRouter, createWebHistory } from 'vue-router'
import DashBoard from '../Vues/Dashboard.vue'
import UserWallet from '../Vues/Wallet.vue'
import UserDocumentation from '../Vues/Documentation.vue'
import UserConfiguration from '../Vues/Configuration.vue'
import ContainerFluid from '../components/ContainerFluid.vue'
import UserLogin from '../Vues/Login.vue'
import UserRegistration from '../Vues/Register.vue'
import ForgotPassword from '../Vues/ForgotPassword.vue'
const routes = [
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: DashBoard
  },
  {
    path: '/wallet',
    name: 'Wallet',
    component: UserWallet
  },
  {
    path: '/configuration',
    name: 'Configuration',
    component: UserConfiguration
  },  {
    path: '/documentation',
    name: 'Documentation',
    component: UserDocumentation
  },
  {
    path: '/',
    name: 'ContainerFluid',
    component: ContainerFluid
  },
  {
    path: '/login',
    name: 'Login',
    component: UserLogin
  },
  {
    path: '/register',
    name: 'Register',
    component: UserRegistration
  },{
    path: '/forgotpassword',
    name: 'Forgotpassword',
    component: ForgotPassword
  },

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router