<template>

    <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow row">

        <!-- Barre de recherche (Component) -->
        <div class="col-9">
            <SearchBar />
        </div>
        <div v-if="isAuth" class="col-3">
            <div>
                <div class="topbar-divider d-none d-sm-block"></div>
                <div class="btn-group open">
                    <a class="btn btn-primary" data-toggle="dropdown" href="#">
                        <i class="fa fa-user fa-fw"></i> User
                        <i class="fa fa-caret-down" title="Toggle dropdown menu"></i>
                    </a>
                    <ul class="dropdown-menu">
                        <li><a>
                                <router-link to="/"><i class="fa fa-pencil fa-fw"></i> Profile</router-link>
                            </a></li>
                        <li><a>
                                <router-link to="/"><i class="fa fa-trash-o fa-fw"></i> Settings</router-link>
                            </a></li>
                        <div class="dropdown-divider"></div>
                        <li @click="logout"><a><i class="fa fa-ban fa-fw"></i> Logout</a></li>
                    </ul>
                </div>
                <div class="topbar-divider d-none d-sm-block"></div>
            </div>
        </div>
        <div v-else class="col-3">
            <div class="row">
                <div class="col-4">
                    <ButtonLink link="/login" nameLink="Login" />
                </div>
                <div class="col-4">
                    <ButtonLink link="/register" nameLink="Register" />
                </div>
            </div>


        </div>

    </nav>

</template>

<script >
import SearchBar from './SearchBar.vue'
import ButtonLink from './Button/ButtonLink.vue'
export default {
    name: "TopBar",
    props: {

    },
    components: { SearchBar, ButtonLink },
    data() {
        return {
            isAuth: this.$store.state.isAuth,
        }

    },
    created() {
        this.isAuth = this.$store.state.isAuth
    },
    mounted() {
        this.isAuth = this.$store.state.isAuth
    },
    methods: {
        logout() {
            this.$store.commit('userLogout')
            this.$router.push("/login")
            this.isAuth = this.$store.state.isAuth
        },
    },
}
</script>


