<template>
    <div class="row">
        <div class="col-6 block-center " v-if="isAuth">
            <TitleTwo title="You are already connected." />
        </div>
        <div class="col-6 block-center " v-else>
            <TitleTwo title="Please register :" />
            <form @submit.prevent="submitForm" class="user col-lg-8 text-center">
                <div class="form-group">
                    <FormField typeField="text" labelName="Username" nameField="username" />
                </div>
                <div class="form-group">
                    <FormField typeField="email" labelName="Email Adress" nameField="email" />
                </div>
                <div class="row">
                    <div class="form-group col-6">
                        <FormField typeField="password" labelName="Password" nameField="password" />
                    </div>
                    <div class="form-group col-6">
                        <FormField typeField="password" labelName="Repeat Password" nameField="reapet-password" />
                    </div>
                </div>
                <ButtonFormSubmit class="btn-block" nameBtn="Sign up" />
                <hr>
                <ParagraphSimple class="d-inline w-text " text="Already have an account ? " />
                <ButtonSmall class="w-text " link="/login" nameBtn="Sign In" />
            </form>
        </div>
    </div>



</template>

<script>
import axios from 'axios'
import TitleTwo from '@/components/Title/TitleTwo.vue';
import ParagraphSimple from '@/components/Paragraph/ParagraphSimple.vue';
import ButtonSmall from '@/components/Button/ButtonSmall.vue';
import ButtonFormSubmit from '@/components/Button/ButtonFormSubmit.vue';
import FormField from '@/components/FormField.vue';
export default {
    name: "UserRegistration",
    data() {
        return {
            isAuth: this.$store.state.isAuth,
            username: "",
            password: "",
            password2: "",
            email: "",
        };
    },
    mounted() {
        this.isAuth = this.$store.state.isAuth;
    },
    methods: {
        submitForm() {
            if (this.password == this.password2) {
                const formData = {
                    username: this.username,
                    password: this.password,
                    email: this.email,
                };
                axios
                    .post("/auth/v1users/", formData)
                    .then(response => {
                        console.log(response);
                        this.$router.push("/");
                    })
                    .catch(error => {
                        console.log(error);
                    });
            }
            else {
                alert("Invalid Password");
            }
        }
    },
    components: { TitleTwo, ParagraphSimple, ButtonSmall, ButtonFormSubmit, FormField }
}
</script>

<style scoped>
.w-text {
    font-size: 1em;
}

.block-center {
    margin: 0 auto;
}
</style>