<template>
    <div class="row m-2">
        <div class="col-12">
            <TitleOne title="Welcome" />

            <div v-if="isAuth">
                <ButtonLink class="mr-2" link="/dashboard" nameLink="Dashboard" />
                <ButtonLink class="mr-2 ml-2" link="/wallet" nameLink="Wallet" />
                <ButtonLink class="mr-2 ml-2" link="/configuration" nameLink="Configuration" />
                <ButtonLink class="mr-2 ml-2" link="/documentation" nameLink="Documentation" />
                <ButtonLink class="ml-2" link="/login" nameLink="Login" />
            </div>
            <template v-else>
                <ParagraphSimple class="w-text d-inline" text="You are not authenticated, please " />
                <ButtonSmall class="w-text " link="/login" nameBtn="Login" />
                <ParagraphSimple class="w-text d-inline" text=" or " />
                <ButtonSmall class="w-text " link="/register" nameBtn="register" />
            </template>
        </div>
    </div>
</template>

<script>
import TitleOne from './Title/TitleOne.vue';
import ParagraphSimple from './Paragraph/ParagraphSimple.vue';
import ButtonSmall from './Button/ButtonSmall.vue';
import ButtonLink from './Button/ButtonLink.vue';
export default {
    name: "ContainerFluid",
    data() {
        return {
            isAuth: this.$store.state.isAuth,
        };
    },
    created() {
        this.isAuth = this.$store.state.isAuth;
    },
    mounted() {
        this.isAuth = this.$store.state.isAuth;
    },
    methods: {
        logout() {
            this.$store.commit("userLogout");
            this.isAuth = this.$store.state.isAuth;
        },
    },
    components: { TitleOne, ParagraphSimple, ButtonSmall, ButtonLink }
}
</script>
<style scoped>
.w-text {
    font-size: 1.2em;

}
</style>