<template>
    <div class="row">
        <div v-if="isAuth">
            <h2>You are already connected.</h2>
        </div>
        <div class="col-12" v-else>
            <h2>Login</h2>
            <div class="row justify-content-center">
                <div class="col-xl-10 col-lg-12 col-md-9 text-center">
                    <div class="row">
                        <div class="col-lg-4 center-form">
                            <form method="post" class="user" @submit.prevent="submitForm">
                                <FormField labelName="Username" typeField="text" nameField="username" />
                                <FormField labelName="Password" typeField="password" nameField="password"/>
                                <ButtonFormSubmit nameBtn="Log In"/>
                            </form>
                                <ButtonSmall nameBtn="Forgot Password ?" link="/forgotpassword"/>
                            <hr>
                                <a class="small">
                                    <router-link to="register">Create an Account!</router-link>
                                </a>
                        </div>
                    </div><br>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import axios from 'axios'
import FormField from '@/components/FormField.vue'
import ButtonFormSubmit from '../components/Button/ButtonFormSubmit.vue'
import ButtonSmall from '@/components/Button/ButtonSmall.vue';
export default {
    name: "UserLogin",
    data() {
        return {
            isAuth: this.$store.state.isAuth,
            username: "",
            password: ""
        };
    },
    created() {
        this.isAuth = this.$store.state.isAuth;
    },
    mounted() {
        this.isAuth = this.$store.state.isAuth;
    },
    methods: {
        submitForm() {
            axios.defaults.headers.common["Authorization"] = "";
            localStorage.removeItem("access");
            const formData = {
                username: this.username,
                password: this.password,
            };
            axios
                .post("auth/v2jwt/create/", formData)
                .then(response => {
                    console.log(response);
                    const access = response.data.access;
                    const refresh = response.data.refresh;
                    this.$store.commit("setAccess", access);
                    this.$store.commit("setRefresh", refresh);
                    axios.defaults.headers.common["Authorization"] = "JWT " + access;
                    localStorage.setItem("access", access);
                    localStorage.setItem("refresh", refresh);
                    localStorage.setItem("isAuth", true);
                    this.$router.push("/");
                })
                .catch(error => {
                    console.log(error);
                });
        }
    },
    components: { FormField, ButtonFormSubmit, ButtonSmall }
}

</script>

<style scoped>
h2 {
    text-align: center;
    color: rgb(4, 11, 55);
}

.center-form {
    margin: auto;
}
</style>